<template>
    <div>
        <div class="top-left">
            <router-link :to="{name: 'home'}" class="btn"><i class="fas fa-arrow-left"></i> Home</router-link>
        </div>
        <div class="align">

            <div class="grid">
                <h1>Ooops....</h1>
                <p>No problem! That's not a big deal!
                </p>
                <form action="#" method="POST" class="form login" @submit.prevent="reset">

                <div class="form__field">
                    <label for="login__email"><i class="icon fas fa-envelope"></i><span class="hidden">Email</span></label>
                    <input id="login__email" type="email" v-model="email" name="username" class="form__input" placeholder="Email" required autofocus>
                </div>
                <div v-if="errors && errors.length" class="error-wrapper has-text-danger">
                    <p v-for="(error, key) in errors" :key="key">{{ error }}</p>
                </div>
                <div class="form__field">
                    <input type="submit" value="Request Login Link">
                </div>

                </form>

                <p class="text--right">
                    Not have an account?
                    <router-link :to="{name: 'register'}">Register here</router-link>.
                </p>

            </div>

            <svg xmlns="http://www.w3.org/2000/svg" class="icons"><symbol id="arrow-right" viewBox="0 0 1792 1792"><path d="M1600 960q0 54-37 91l-651 651q-39 37-91 37-51 0-90-37l-75-75q-38-38-38-91t38-91l293-293H245q-52 0-84.5-37.5T128 1024V896q0-53 32.5-90.5T245 768h704L656 474q-38-36-38-90t38-90l75-75q38-38 90-38 53 0 91 38l651 651q37 35 37 90z"/></symbol><symbol id="lock" viewBox="0 0 1792 1792"><path d="M640 768h512V576q0-106-75-181t-181-75-181 75-75 181v192zm832 96v576q0 40-28 68t-68 28H416q-40 0-68-28t-28-68V864q0-40 28-68t68-28h32V576q0-184 132-316t316-132 316 132 132 316v192h32q40 0 68 28t28 68z"/></symbol><symbol id="user" viewBox="0 0 1792 1792"><path d="M1600 1405q0 120-73 189.5t-194 69.5H459q-121 0-194-69.5T192 1405q0-53 3.5-103.5t14-109T236 1084t43-97.5 62-81 85.5-53.5T538 832q9 0 42 21.5t74.5 48 108 48T896 971t133.5-21.5 108-48 74.5-48 42-21.5q61 0 111.5 20t85.5 53.5 62 81 43 97.5 26.5 108.5 14 109 3.5 103.5zm-320-893q0 159-112.5 271.5T896 896 624.5 783.5 512 512t112.5-271.5T896 128t271.5 112.5T1280 512z"/></symbol></svg>

        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            email: '',
            errors: []
        }
    },
    created() {
        this.$root.setTitle('Password Recovery')
    },
    methods: {
        reset: function () {
            let data = {
                email: this.email
            }

            this.$store.dispatch('reset', data)
            .then(resp => {
                this.$toastr('success', 'A reset link was sent to your email.', 'Success!')
                this.$router.push('/')
            }).catch(error => {
                this.errors = []
                if (error.response.data.error) {
                    this.errors.push(error.response.data.error)
                } else if (error.response.data.errors) {
                    for (let key in error.response.data.errors) {
                        if (key && key.length) this.errors.push(error.response.data.errors[key][0])
                    }
                }
            })
        }
    }
}
</script>

<style scoped src="../../assets/css/form.css"></style>
